import React from 'react'
import styled from 'styled-components'
import { isNil, length, keys, pathOr, gt, __, map, filter } from 'ramda'
import cx from 'classnames'
import cheerio from 'cheerio'
import { Row, Col, Divider as AntDivider, Form } from 'antd'
import { useReportJson } from '@/services/report'
import { useBreadcrumb } from '@/hooks/useApp'
import { useAuth } from '@/hooks/useAuth'
import { createFormItemLayout } from '@/utils/form'
import { Snippets, SnippetProps, MedicalOrder } from '@/constants/types'
import { snippetTypes } from '@/components/snippet-template-list'
import { Input } from '@/components/input'
import { mapIndexed, toThousandSeparator } from '@/utils/webHelper'
import { Divisions } from '@/constants/enums'
const { compose } = require('ramda')

type Props = {
  className?: string
  style?: React.CSSProperties
  data: {
    template: any
    snippetList: Snippets
    medical: MedicalOrder | null
  }
  exporting?: boolean
}

const formItemLayout = createFormItemLayout(8)
const shortFormItemLayout = createFormItemLayout(3)

const FormItem = styled(Form.Item)`
  margin-bottom: 12px;
`

const Divider = styled(AntDivider)`
  margin: 16px 0;
`

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
    font-size: 14px;
  }

  .ant-card-body {
    padding-top: 6px;
    padding-bottom: 8px;
    padding-left: 18px;
  }
`

export default function ReportTypingPreview({ className, style, data }: Props) {
  useBreadcrumb([{ name: '' }, { name: '' }])

  const { json } = useReportJson({ orreqno: data.template.orreqno })

  const { snippetList, medical } = data

  const { user } = useAuth()

  const getHtmlText = (
    { html = '', valuesJson = [] }: SnippetProps,
    idx?: number
  ) => {
    const $ = cheerio.load(html)

    const getValue = (idx: number, val: any) => {
      // PDF 
      return pathOr(toThousandSeparator(val), [idx, 'codexxx'], valuesJson)
    }

    $('.template-cloze__control').each((idx, el) => {
      const $el = $(el)
      $el.replaceWith(
        (el as any).name === 'select'
          ? ' '
          : '' + getValue(idx, $el.val()) + ' '
      )
    })

    const div = document.createElement('div')
    div.innerHTML = $.html()

    const text = div.textContent || div.innerText || ''
    div.remove()

    return `${!idx ? '' : idx + '. '}${text}`
  }

  const _snippetList = pathOr(snippetList, ['setting'], json)

  const renderReport = ({ useCode = false }: any = {}) => {
    return (
      <>
        <Row className="mb-3">
          <Col span={24}>
            <div className="text-xl text-center mt-2">
              
              <span className="mx-6">
                {user.division === Divisions.RAD ? '' : ''}
              </span>
              
            </div>
          </Col>
        </Row>
        <Divider />
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.hbed} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.hnamec} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.hhisnum} />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.hsex} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.cfinancl} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input
                readOnly
                bordered={false}
                value={medical?.hbirthdt || medical?.hbirthd2}
              />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.orreqno} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...shortFormItemLayout} label="">
              <Input readOnly bordered={false} value={medical?.csect} />
            </FormItem>
          </Col>
          <Col span={8}>
            {user.division === Divisions.RAD ? (
              <FormItem {...formItemLayout} label="">
                <Input readOnly bordered={false} value={'xxxx'} />
              </FormItem>
            ) : (
              <FormItem {...formItemLayout} label="">
                <Input readOnly bordered={false} value={medical?.orworkno} />
              </FormItem>
            )}
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormItem {...shortFormItemLayout} label="">
              <div
                dangerouslySetInnerHTML={{ __html: `${medical?.orword}` }}
                style={{
                  maxWidth: '100%',
                  whiteSpace: 'initial',
                  borderBottom: '1px solid #d9d9d9',
                  paddingBottom: 5,
                  paddingLeft: 5,
                  marginLeft: -15,
                }}
              />
            </FormItem>
          </Col>
        </Row>
        {user.division === Divisions.RAD && (
          <Row>
            <Col span={24}>
              <FormItem {...shortFormItemLayout} label="">
                <Input readOnly bordered={false} value={'xxxx'} />
              </FormItem>
            </Col>
          </Row>
        )}
        <Divider />
        <Row>
          <Col span={24}>
            <SnippetPreviewWrapper>
              {compose(
                map((key: string) => (
                  <div
                    key={key}
                    css={{
                      fontSize: 15,
                      lineHeight: 1.65,
                      color: 'rgba(0, 0, 0, 0.85)',
                    }}>
                    {compose(
                      gt(__, 0),
                      length,
                      pathOr([], [key])
                    )(snippetList) && (
                      <h2
                        className="my-1 font-normal underline"
                        css={{ fontSize: 16 }}>
                        {key}
                      </h2>
                    )}
                    {compose(
                      mapIndexed(
                        ({ setting, ...x }: SnippetProps, idx: number) => (
                          <div key={x.id}>
                            {(() => {
                              let textOnly = key === 'Gross'
                              let className = `ml-0`

                              if (x.inline) {
                                return (
                                  <div
                                    className={className}
                                    dangerouslySetInnerHTML={{
                                      __html: getHtmlText(
                                        x as any,
                                        textOnly ? undefined : idx + 1
                                      ),
                                    }}
                                  />
                                )
                              }

                              if (x.type === snippetTypes.HEADER) {
                                return (
                                  <div className={className}>
                                    {idx + 1}. {setting.text}
                                  </div>
                                )
                              }

                              return (
                                <div className={className}>
                                  {textOnly ? '' : idx + 1 + '.'} {setting.text}
                                  : {setting.value}
                                </div>
                              )
                            })()}
                          </div>
                        )
                      ),
                      filter(
                        ({ setting, ...x }: SnippetProps) =>
                          (!isNil(setting) && !isNil(setting.value)) ||
                          x.type === snippetTypes.HEADER ||
                          x.inline
                      ),
                      pathOr([], [key])
                    )(_snippetList)}
                  </div>
                )),
                keys
              )(_snippetList)}
            </SnippetPreviewWrapper>
          </Col>
        </Row>
        <Divider />
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <FormItem {...formItemLayout} label="">
              <Input bordered={false} />
            </FormItem>
          </Col>
          <Col span={12}>
            <FormItem {...formItemLayout} label="">
              <Input bordered={false} />
            </FormItem>
          </Col>
        </Row>
        <Divider />
        <Row className="mb-4">
          <Col span={24}>
            <div className="text-center">000001</div>
          </Col>
        </Row>

        <Row className="mt-4 hidden">
          <Col span={24}>
            <SnippetPreviewWrapper>
              {compose(
                map((key: string) => (
                  <div
                    key={key}
                    css={{
                      fontSize: 15,
                      lineHeight: 1.65,
                      color: 'rgba(0, 0, 0, 0.85)',
                    }}>
                    {compose(
                      gt(__, 0),
                      length,
                      pathOr([], [key])
                    )(snippetList) && (
                      <h2
                        className="my-1 mt-5 font-normal underline"
                        css={{ fontSize: 16 }}>
                        {key}
                      </h2>
                    )}
                    {compose(
                      mapIndexed(
                        ({ setting, ...x }: SnippetProps, _idx: number) => (
                          <span key={x.id}>
                            {(() => {
                              let className = `ml-0`

                              let textOnly = false

                              if (
                                key === 'Pathological diagnosis' ||
                                key === 'Notes'
                              ) {
                                textOnly = true
                              }

                              if (x.inline) {
                                let Tag: any = textOnly ? 'span' : 'div'
                                return (
                                  <Tag
                                    className={className}
                                    dangerouslySetInnerHTML={{
                                      __html: getHtmlText(
                                        x as any,
                                        textOnly ||
                                          (key === 'Gross' &&
                                            length(_snippetList[key]) === 1)
                                          ? undefined
                                          : _idx + 1
                                      ),
                                    }}
                                  />
                                )
                              }

                              if (x.type === snippetTypes.HEADER) {
                                return textOnly ? (
                                  `${setting.text}.`
                                ) : (
                                  <div className={className}>
                                    {_idx + 1}. {setting.text}
                                  </div>
                                )
                              }

                              return textOnly ? (
                                `${setting.text} ${setting.value}, `
                              ) : (
                                <div className={className}>
                                  {_idx + 1}. {setting.text}: {setting.value}
                                </div>
                              )
                            })()}
                          </span>
                        )
                      ),
                      filter(
                        ({ setting, ...x }: SnippetProps) =>
                          (!isNil(setting) && !isNil(setting.value)) ||
                          x.type === snippetTypes.HEADER ||
                          x.inline
                      ),
                      pathOr([], [key])
                    )(_snippetList)}
                  </div>
                )),
                keys
              )(_snippetList)}
            </SnippetPreviewWrapper>
          </Col>
        </Row>
      </>
    )
  }

  return (
    <>
      <div style={style} className={cx('js-export-pdf', className)}>
        {renderReport()}
      </div>
    </>
  )
}
