import React from 'react'
import cx from 'classnames'
import { map } from 'ramda'
import { Form, Tooltip } from 'antd'
import { DoubleRightOutlined } from '@ant-design/icons'
import Collapse from '@/components/collapse'
import { Checkbox, CheckboxGroup } from '@/components/checkbox'
import Radio from '@/components/radio'
import { reportAnnotations, Divisions } from '@/constants/enums'
import useAuth from '@/hooks/useAuth'
import { MedicalOrder } from '@/constants/types'
import { reportStatusEnums } from '@/constants/enums'
import { rules } from '@/utils/form'

type Props = {
  data?: MedicalOrder | null
  onSave?: () => void
  onPhraseOpen?: () => void
  onToggle?: () => void
}

export default function ReportMedicalOrder({ data, onToggle }: Props) {
  const { user } = useAuth()
  return (
    <Collapse
      activeKey={[1, 2, 3, 4]}
      expandIcon={() => null}
      className="shadow-2xl">
      <Collapse.Panel
        header={
          <div className="text-center relative">
            <Tooltip title="">
              <DoubleRightOutlined
                className="absolute"
                style={{ top: 5, left: 15 }}
                onClick={onToggle}
              />
            </Tooltip>
            
          </div>
        }
        key={1}>
        <div className="border-b border-gray-600 p-3">
          {data?.orreqno}
        </div>
        {user.division === Divisions.PATH && (
          <div className="border-b border-gray-600 p-3">
            {data?.orworkno}
          </div>
        )}
        <div className="border-b border-gray-600 p-3">
          {data?.hnamec}
        </div>
        <div className="border-b border-gray-600 p-3">{data?.hsex}</div>
        <div className="border-b border-gray-600 p-3">
          {data?.csect}
        </div>
        <div className="border-b border-gray-600 p-3">
          {data?.hhisnum}
        </div>
        <div className="border-b border-gray-600 p-3">
          {data?.hnursta}
        </div>
        <div className="border-b border-gray-600 p-3">
          {data?.hbed}
        </div>
        <div className="border-b border-gray-600 p-3">
          {data?.hbirthdt || data?.hbirthd2}
        </div>
        <div className="border-b border-gray-600 p-3">
          {data?.orproced}
        </div>
        <div className="border-b border-gray-600 p-3">
          {user.name}
        </div>
        <div className="border-b border-gray-600 p-3">{user.name}</div>
      </Collapse.Panel>
      <Collapse.Panel
        header={<div className="text-center"></div>}
        key={2}>
        <Form.Item noStyle name="status" rules={[rules.required]}>
          <Radio.Group className="pl-3">
            {map((s: { id: number; name: string }) => (
              <Radio key={s.id} className="block mb-1" value={s.id}>
                {s.name}
              </Radio>
            ))(reportStatusEnums)}
          </Radio.Group>
        </Form.Item>
      </Collapse.Panel>
      {user.division === Divisions.PATH && (
        <Collapse.Panel
          header={<div className="text-center"></div>}
          key={3}>
          <Form.Item noStyle name="annotations">
            <CheckboxGroup>
              {reportAnnotations.map((x, idx) => (
                <Checkbox
                  key={`annotations-${idx}`}
                  className={cx('block ml-0 pl-2', {
                    'mt-1': idx > 0,
                  })}
                  defaultChecked={false}
                  value={`${idx}`}>
                  {x}
                </Checkbox>
              ))}
            </CheckboxGroup>
          </Form.Item>
        </Collapse.Panel>
      )}
      <Collapse.Panel
        header={<div className="text-center"></div>}
        key={4}>
        <div className="pl-6">
          <code className="bg-yellow-400 px-1">CTRL+S</code> 
        </div>
        <div className="pl-6 pt-2">
          <code className="bg-yellow-400 px-1">F9</code> 
        </div>
      </Collapse.Panel>
    </Collapse>
  )
}
